import React from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';

const validationSchema = Yup.object().shape({
  email: Yup.string()
    .email('Nieprawidowy adres e-mail')
    .required('To pole jest wymagane'),
  password: Yup.string()
    .min(6, 'Haso musi mie co najmniej 6 znakw')
    .max(12, 'Haso nie moe mie wicej ni 12 znakw')
    .required('To pole jest wymagane'),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('password'), null], 'Hasa musz by takie same')
    .required('To pole jest wymagane'),
  firstName: Yup.string()
    .min(1, 'Imi musi mie co najmniej 1 znak')
    .max(12, 'Imi nie moe mie wicej ni 12 znakw')
    .required('To pole jest wymagane'),
});

const initialValues = {
  email: '',
  password: '',
  confirmPassword: '',
  firstName: '',
};

const RegistrationForm = () => {
  const handleSubmit = values => {
    //  logika obsugi formularza. wysanie danych na serwer
    console.log('Dane formularza:', values);
  };

  return (
    <div>
      <img src="" alt="Logo" />
      {/* podmieni logo! z loginform  */}
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={handleSubmit}
      >
        <Form>
          <div>
            <label htmlFor="email">E-mail</label>
            <Field type="email" id="email" name="email" />
            <svg id="message" /* Tu doda definicj SVG z loginform*/ />
            <ErrorMessage name="email" component="div" />
          </div>

          <div>
            <label htmlFor="password">Haso</label>
            <Field type="password" id="password" name="password" />
            <ErrorMessage name="password" component="div" />
          </div>

          <div>
            <label htmlFor="confirmPassword">Potwierd haso</label>
            <Field
              type="password"
              id="confirmPassword"
              name="confirmPassword"
            />
            <ErrorMessage name="confirmPassword" component="div" />
          </div>

          <div>
            <label htmlFor="firstName">Imi</label>
            <Field type="text" id="firstName" name="firstName" />
            <ErrorMessage name="firstName" component="div" />
          </div>

          <button type="submit">Zarejestruj</button>
        </Form>
      </Formik>
      <button type="button">Zaloguj</button>
    </div>
  );
};

export default RegistrationForm;
